@import '../../Variables.scss';

#contact {
	*,
	*:before,
	*:after {
		box-sizing: border-box;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}

	background: $background-color;
	font-size: 12px;
	font-family: 'Montserrat', sans-serif;
	font-weight: 700;
	letter-spacing: 1.4px;
	min-height: unset;
	padding-bottom: 120px;

	form {
		margin-top: 60px;
	}

	button,
	input {
		font-family: 'Montserrat', sans-serif;
		font-weight: 700;
		letter-spacing: 1.4px;
	}

	.background {
		display: flex;
	}

	.container {
		flex: 0 1 700px;
		margin: auto;
		padding: 10px;
	}

	.screen {
		position: relative;
		background: #3e3e3e;
		border-radius: 15px;
	}

	.screen:after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 20px;
		right: 20px;
		bottom: 0;
		border-radius: 15px;
		box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
		z-index: -1;
	}

	.screen-header {
		display: flex;
		align-items: center;
		padding: 10px 20px;
		background: #4d4d4f;
		border-top-left-radius: 15px;
		border-top-right-radius: 15px;
	}

	.screen-header-left {
		margin-right: auto;
	}

	.screen-header-button {
		display: inline-block;
		width: 8px;
		height: 8px;
		margin-right: 3px;
		border-radius: 8px;
		background: white;
	}

	.screen-header-button.close {
		background: #ed1c6f;
	}

	.screen-header-button.maximize {
		background: #e8e925;
	}

	.screen-header-button.minimize {
		background: #74c54f;
	}

	.screen-header-right {
		display: flex;
	}

	.screen-header-ellipsis {
		width: 3px;
		height: 3px;
		margin-left: 2px;
		border-radius: 8px;
		background: #999;
	}

	.screen-body {
		display: flex;
	}

	.screen-body-item {
		flex: 1;
		padding: 50px;
	}

	.screen-body-item.left {
		display: flex;
		flex-direction: column;
	}

	.app-title {
		display: flex;
		flex-direction: column;
		position: relative;
		color: $primary-color;
		font-size: 26px;
	}

	.app-contact,
	a {
		margin-top: 3px;
		font-size: 0.9em;
		color: #888;
	}

	.app-form-group {
		margin-bottom: 15px;
	}

	.app-form-group.message {
		margin-top: 40px;
	}

	.app-form-group.buttons {
		margin-bottom: 0;
		text-align: right;
	}

	.app-form-control {
		width: 100%;
		padding: 10px 0;
		background: none;
		border: none;
		border-bottom: 1px solid #666;
		color: #ddd;
		font-size: 14px;
		outline: none;
		transition: border-color 0.2s;
	}

	.app-form-control::placeholder {
		color: #666;
	}

	.app-form-control:focus {
		border-bottom-color: #ddd;
	}

	.app-form-button {
		background: none;
		border: none;
		color: $primary-color;
		font-size: 14px;
		cursor: pointer;
		outline: none;
	}

	.app-form-button:hover {
		color: $primary-color;
	}

	@media screen and (max-width: 520px) {
		.screen-body {
			flex-direction: column;
		}

		.screen-body-item.left {
			margin-bottom: 30px;
		}

		.app-title {
			flex-direction: row;
		}

		.app-title span {
			margin-right: 12px;
		}

		.app-title:after {
			display: none;
		}
	}

	@media screen and (max-width: 600px) {
		.screen-body {
			padding: 40px;
		}

		.screen-body-item {
			padding: 0;
		}
	}
}
